.node_form{
    width: 70%;
    padding: 20px;
    display: grid;
    grid-template-columns: 1fr;
    margin: auto;
}
.node_form input{
    height: 40px;
    padding: 5px;
    margin-top: 10px;
    margin-bottom: 20px;
    font-size: 1.2rem;
    opacity: 0.7;
    color: rgb(62, 61, 61);
    outline: none;
}
.node_form button{
    height: 40px;
    font-size: 1.2rem;

}